
#app {
  font-family: 'Futura', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
td {
  text-align: left;
}
.panel.panel-default {
  padding: 2% 0;
}
label, .btn.btn-primary {
  margin: 5px 10px;
}
.btn.btn-primary {
  background-color: blue;
  border-color: blue;
}
.btn.btn-primary:hover {
  background-color: red;
  border-color: red;
}



